
import * as licensing from '@progress/kendo-licensing';

export const __meta__ = {
    id: "licensing",
    name: "Licensing",
    category: "framework",
    description: "The licensing mechanism for Kendo UI for jQuery.",
    depends: []
};

/**
 * The package metadata.
 * @type {import('@progress/kendo-licensing').PackageMetadata}
 */
let _parsedPackageMetadata = null;

try {
    _parsedPackageMetadata = Object.freeze(JSON.parse('$PACKAGE_METADATA'));
} catch (e) {
    _parsedPackageMetadata = Object.freeze({
        name: '@progress/kendo-ui',
        productName: 'Kendo UI for jQuery',
        productCode: 'KENDOUICOMPLETE',
        productCodes: ["KENDOUICOMPLETE", "KENDOUI", "UIASPCORE", "KENDOMVC", "KENDOUIMVC"],
        redistributedBy: ['KENDOUI', 'UIASPCORE', 'KENDOMVC', 'KENDOUIMVC'],
        publishDate: 0,
        version: '0.0.0',
        licensingDocsUrl: 'https://docs.telerik.com/kendo-ui/intro/installation/using-license-code?utm_medium=product&utm_source=kendojquery&utm_campaign=kendo-ui-jquery-purchase-license-keys-warning'
    });
}

export const packageMetadata = _parsedPackageMetadata;

const LICENSING_LINK = 'https://docs.telerik.com/kendo-ui/intro/installation/using-license-code?utm_medium=product&utm_source=kendojquery&utm_campaign=kendo-ui-jquery-purchase-license-keys-banner';

function shouldHideWatermarkAndBanner(packageMetadata) {
    const disabledByHost = [/telerik\.com/, /progress\.com/, /stackblitz\.io/, /csb\.app/, /telerik\.io/]
        .map(r => r.test(window.location.hostname))
        .indexOf(true) > -1;

    return disabledByHost || false;
}

const addWatermarkOverlay = function(el) {
    // avoid adding the watermark
    if (shouldHideWatermarkAndBanner(packageMetadata)) {
        return;
    }

    const watermark = document.createElement("div");
    watermark.style.setProperty("position", "absolute");
    watermark.style.setProperty("width", "100%");
    watermark.style.setProperty("height", "100%");
    watermark.style.setProperty("pointer-events", "none");
    watermark.style.setProperty("top", "0");
    watermark.style.setProperty("left", "0");
    watermark.style.setProperty("right", "0");
    watermark.style.setProperty("bottom", "0");
    const watermarkRoot = watermark.attachShadow({ mode: "closed" });
    const watermarkElement = document.createElement("div");
    watermarkElement.style.setProperty("position", "absolute");
    watermarkElement.style.setProperty("top", "0");
    watermarkElement.style.setProperty("left", "0");
    watermarkElement.style.setProperty("right", "0");
    watermarkElement.style.setProperty("bottom", "0");
    watermarkElement.style.setProperty("pointer-events", "none");
    watermarkElement.style.setProperty("z-index", "101");
    watermarkElement.style.setProperty("opacity", "0.2");
    watermarkElement.style.setProperty("width", "100%");
    watermarkElement.style.setProperty("height", "100%");
    watermarkElement.style.setProperty("background-image", "url('data:image/png;base64,')");

    watermarkRoot.appendChild(watermarkElement);
    el.appendChild(watermark);

    const mo = new MutationObserver(function(e) {
        const record = e[0];

        if (record.removedNodes && Array.from(record.removedNodes).indexOf(watermark) > -1) {
            setTimeout(() => {
                el.appendChild(watermark);
            }, 100);
        }

        if (record.type === "attributes" && record.target === watermark) {
            setTimeout(() => {
                watermark.removeAttribute(record.attributeName);
            }, 100);
        }
    });

    mo.observe(el, { childList: true, attributes: true, subtree: true });
};

const generateLicenseMessage = function(status) {
    const licensingLink = status?.licensingDocsUrl || LICENSING_LINK;
    const productName = status?.productName || 'Kendo UI for jQuery';
    const defaultMessage = `We couldn't verify your <a href=${licensingLink}>license key</a> for ${productName}. Please see the browser console for details and resolution steps.`;

    if (status?.message?.notificationMessage) {
        return status.message.notificationMessage;
    }
    return defaultMessage;
};

const showBanner = function() {
    // avoid showing banner
    if (shouldHideWatermarkAndBanner(packageMetadata)) {
        return;
    }

    const status = licensing.getLicenseStatus(window._wrappersMetadata || packageMetadata);
    const message = generateLicenseMessage(status);

    let bannerElement = document.querySelector("#k-license-banner");

    if (!bannerElement) {
        bannerElement = document.createElement("div");

        bannerElement.setAttribute("id", "k-license-banner");
        bannerElement.style.setProperty("width", "559px");
        bannerElement.style.setProperty("display", "flex");
        bannerElement.style.setProperty("justify-content", "center");
        bannerElement.style.setProperty("align-items", "center");
        bannerElement.style.setProperty("position", "absolute");
        bannerElement.style.setProperty("top", "16px");
        bannerElement.style.setProperty("right", "16px");
        bannerElement.style.setProperty("padding", "12px");
        bannerElement.style.setProperty("border-radius", "4px");
        bannerElement.style.setProperty("box-shadow", "0px 4px 5px 0px rgba(0, 0, 0, 0.04), 0px 2px 4px 0px rgba(0, 0, 0, 0.03)");
        bannerElement.style.setProperty("font-size", "14px");
        bannerElement.style.setProperty("font-weight", "14px");
        bannerElement.style.setProperty("line-height", "20px");
        bannerElement.style.setProperty("background-color", "#FFC000");
        bannerElement.style.setProperty("color", "#1E1E1E");
        bannerElement.style.setProperty("z-index", "999");

        bannerElement.innerHTML = `
            <span id="text">
                <svg width="16" height="14" viewBox="0 0 16 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd" clip-rule="evenodd" d="M8 0L0 14H16L8 0ZM7 5V10H9V5H7ZM7 13V11H9V13H7Z" fill="#1E1E1E"/>
                </svg>
            </span>

            <span>
                ${message}
            </span>

            <div id="button-wrapper">
                <button id="close-btn" title="Close">
                    <svg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M10 1.41562L6.41563 5L10 8.58438L8.58438 10L5 6.41563L1.41562 10L0 8.58438L3.58437 5L0 1.41562L1.41562 0L5 3.58437L8.58438 0L10 1.41562Z" fill="#1E1E1E"/>
                    </svg>
                </button>
            </div>
            `;

        const text = bannerElement.querySelector("#text");
        text.style.setProperty("display", "flex");
        text.style.setProperty("align-self", "center");
        text.style.setProperty("margin-right", "8px");

        const buttonWrapper = bannerElement.querySelector("#button-wrapper");
        buttonWrapper.style.setProperty("display", "flex");
        buttonWrapper.style.setProperty("align-items", "center");
        buttonWrapper.style.setProperty("margin-left", "24px");

        const closeBtn = bannerElement.querySelector("#close-btn");
        closeBtn.style.setProperty("display", "inline-flex");
        closeBtn.style.setProperty("position", "relative");
        closeBtn.style.setProperty("border", "none");
        closeBtn.style.setProperty("background-color", "transparent");
        closeBtn.style.setProperty("cursor", "pointer");
        closeBtn.style.setProperty("outline", "none");
        closeBtn.style.setProperty("padding", "5px");
        closeBtn.style.setProperty("border-radius", "4px");
        closeBtn.style.setProperty("transition", "color 0.2s ease-in-out");

        closeBtn.addEventListener("click", function() {
            bannerElement.setAttribute("hidden", true);
        });

        document.body.appendChild(bannerElement);
    }
};

export const validatePackage = function() {
    return licensing.validatePackage(window._wrappersMetadata || packageMetadata);
};

export const setScriptKey = licensing.setScriptKey;

export const addWatermarkOverlayAndBanner = function(el) {
    addWatermarkOverlay(el);
    showBanner();
};

if (!window.KendoLicensing) {
    window.KendoLicensing = {
        setScriptKey
    };
}
